<template>

  <!-- class명 조건부로 넣기 { 클래스명 : 조건 } -->
  <transition name="fade">
    <Modal
    @closeModal="모달창열렸니=false"
    :원룸들="원룸들" :누른거="누른거" :모달창열렸니="모달창열렸니"/>
  </transition>

  <div class="menu">
    <a v-for="a in 메뉴들" :key="a"> {{ a }} </a>
  </div>
  
  <!-- <Discount v-bind="오브젝트" :이름="오브젝트.name" :나이="오브젝트.age" /> -->
  <Discount :amount="amount" v-if="showDiscount == true" />


  <button @click="priceSort()">가격순정렬</button>
  <button @click="sortBack()">되돌리기</button>


  <Card
  @openModal="모달창열렸니 = true; 누른거 = $event"
  :원룸="원룸들[i]" v-for="(원룸,i) in 원룸들" :key="원룸"
  />

  <!--
  컴포넌트 사용 방법 
  1. (Card.vue) html 템플릿 복붙, export default > name: '이름' 지정
  2. (App.vue) import 컴포넌트 from './Card.Vue'
  3. (App.vue) export default > components: { 작명: 컴포넌트이름 }
  4. (App.vue) html에 컴포넌트 태그 추가
  -->

  <!--
  props : 부모(App)에서 자식(Card)으로 데이터 전송할때
  1. (App.vue) 전송:: Card 태그 안에서 전송 :원름="원룸들[0]" 
  2. (Card.vue) 등록:: 수신 export default > props: { 데이터이름: 자료형 }
  3. (Card.vue) 사용:: 템플릿 안에서 데이터 사용
  -->

</template>

<script>
import data from './oneroom.js';
import Discount from './Discount.vue';
import Modal from './Modal.vue';
import Card from './Card.vue';


export default {
  name: 'App',
  
  data() { // 데이터 보관함이 있어야됨!
    return { // object 자료형식으로
      showDiscount: true,
      원룸들오리지널: [...data],
      오브젝트: {name: 'Kim', age: 20},
      모달창열렸니: false,
      신고수: [0,0,0],
      products: ['역삼동원룸', '천호동원룸','마포구원룸'],
      메뉴들: ['Home', 'Shop', 'About'],
      원룸들: data,
      누른거: 0,
      amount: 30,
    }
  },

  methods: {
    increase() {
      this.신고수 += 1;
    },
    priceSort() {
      console.log(this.원룸들);
      this.원룸들.sort(function(a,b){
        return a.price - b.price
      })
    },
    sortBack() {
      this.원룸들 = this.원룸들오리지널;
    }
  },

  created(){
    // ** html 생성전, 데이터만 존재할때
  },

  mounted(){
    // ** App.vue가 mount 되자마자 실행해주세요~
    setInterval(() => {
      this.amount--;
    }, 1000);
  },

  updated(){

  },

  // mounted(){
  //   setTimeout(() => {
  //     this.showDiscount = false
  //   }, 2000);
  // },
  
  components: {
    Discount: Discount,
    Modal: Modal,
    Card: Card,
  }
}
</script>

