<template>
    <div class="black-bg" v-if="모달창열렸니 == true">
    <div class="white-bg">
      <button @click="$emit('closeModal')">닫기</button>
      <img :src="원룸들[누른거].image" class="room-img" />
      <h4>{{ 원룸들[누른거].title }} </h4>
      <p>{{ 원룸들[누른거].content }}</p>
      <!-- <input @input="month = $event.target.value"> -->
      <input v-model.number="month">
      <!-- <input type="range" min="1" max="12"> -->
      <p> {{ month }} 개월 선택함 : {{ 원룸들[누른거].price * month }} 원 </p>
      <Discount/>
    </div>
  </div>
</template>

<script>
import Discount from './Discount.vue';
export default {
    name: 'Modal', 
    data(){
      return {
        month: 1,
        date: 123
      }
    },

    watch: {
      month(a){
        //month 라는 데이터가 변경될때마다 여기있는 코드 실행
        if( a >= 13 ) {
          alert('13이상 입력하지 마셈')
        }
        // 글자 입력시 alert 띄우고 month 값 1로 돌리기
        if( isNaN(a) == true ) {
          alert('글자 입력하지 마셈');
          this.month = 1;
        }
      },
    },
    
    props: {
      원룸들: Array,
      누른거: Number,
      모달창열렸니: Boolean,
    },

    beforeUpdate() {
      if(this.month == 2){
        alert('2는 너무 적음.. 안팔아요');
        this.month = 3;
      }
    },

    components: {
      Discount: Discount,
    }
}
</script>

